.Overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.8);
    z-index: 1200;
  }
  
  .Modal {
    position: relative;
    background-color: aliceblue;
    padding: 20px;
  }
  .Modal .img {
    max-width: 15%;
    max-height: 15%;
  }